/* Mobile Responsive Handling */
@media screen and (max-width: 768px) {
    /* NAV */
    .nav-menu {
      display: none;
    }
    
    .navbar {
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
    }
    
    .navbar-toggle {
      display: block;
      font-size: 1.5rem;
      color: #fff;
      cursor: pointer;
    }
    
    .navbar-toggle:hover {
      color: #ccc;
    }
    
    .navbar-menu {
      display: none;
      margin: 1rem 0 0;
      padding: 0;
      list-style: none;
    }
    
    .navbar-menu li {
      margin: 0;
    }
    
    .navbar-menu li a {
      display: block;
      padding: 0.5rem 1rem;
      background-color: #444;
      color: #fff;
      text-decoration: none;
    }
    
    .navbar-menu li a:hover {
      background-color: #555;
    }
    
    .navbar-toggle.active + .navbar-menu {
      display: block;
    }



    /*SECTIONS*/
    section {
      padding: 1rem;
      margin: 1rem 0;
    }
  
    section h2 {
      font-size: 1.5rem;
    }
  
    section p {
      font-size: 1rem;
    }

    .sec-background {
      margin-top: 10px;
    }

    .service-block {
      width: 100%;
      height: auto;
    }

    .Home .imgdiv1 {
      top: 7%; /* Adjust the value as needed */
    }
    
    .imgdiv1 .centerLogo {
      width: 60%; /* Adjust the width as needed */
      height: auto; /* Adjust the height as needed */
    }
  
    .Home .scrollDown {
      top: 20%; /* Adjust the value as needed */
    }
  
    .scrollDown .scrollInd {
      width: 22%; /* Adjust the width as needed */
      height: auto; /* Adjust the height as needed */
    }
  
    .contactSection {
      bottom: 4rem; /* Adjust the value as needed */
      right: 4rem; /* Adjust the value as needed */
    }
  
    .contactSection .c-img {
      width: 40%; /* Adjust the width as needed */
    }

    .contactCard {
      width: 80%; /* Set the card's width to match its parent's width */
      top: 62%; /* Adjust the value as needed */
      height: 150px;
      left: 50%; /* Center the card horizontally */
      transform: translate(-50%, 50%); /* Shift the card's position to center it */
      margin: 0 auto; /* Center the card horizontally */
    }

    .cardHeader {
      font-size: 1rem; /* Adjust the font size for mobile */
    }
  
    .contact-row .email {
      font-size: 12px; /* Adjust the font size for mobile */
    }
  
    .contact-row {
      font-size: 11px; /* Adjust the font size for mobile */
    }
  
    .contact-row .contact-label {
      font-size: 11px; /* Adjust the font size for mobile */
    }

    .contact-text .contact-text {
      font-size: 9px;
    }
  
    .copy-button {
      padding: 0.25rem 0.5rem; /* Adjust the padding for mobile */
      font-size: 10px; /* Adjust the font size for mobile */
    }

    .info-card {
      /* Your existing info-card styles here */
      padding: 1rem; /* Add more padding for mobile */
    }
  
    .info-card .sec-title {
      font-size: 70px; /* Adjust the font size for mobile */
      padding-bottom: 25px;
    }
  
    .info-card .sec-paragraph {
      font-size: 20px; /* Adjust the font size for mobile */
      padding-bottom: 20px; /* Adjust padding for mobile */
    }


    /*FOOTER*/
    .footer {
      flex-direction: column;
    }
    .footer p {
      margin-bottom: 1rem;
    }
}


/* ANIMATIONS */

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-1rem);
  }
  100% {
    transform: translateY(0);
  }
}


/* MAIN APP Styling*/

.App {
  text-align: center;
}

/*BODY STYLING*/

html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  min-height: 100%;
  padding-bottom: 5rem;
  background-color: #fff;
  transition: transform 0.3s ease-out;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

