.navbar {
  background-color: transparent; /*#333*/
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem;
  width: 100%;
  height: 100px
}

.nav-menu {
  display: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav-menu li {
  margin: 0 1rem;
}

.nav-menu li a {
  color: #817f7f;
  text-decoration: none;
  font-weight: bold;
  text-transform: uppercase;
}

/* SECTIONS */

section {
  background-color: #f7f7f7;
  padding: 0rem;
  margin: 0rem 0;
}

section h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

section p {
  font-size: 1.2rem;
  line-height: 1.5;
}

.Home {
  display: flex;
  min-height: 100vh;
  background-image: url('https://cklandscaping.s3.amazonaws.com/static/media/KT_MODIFIED.1ffd0f384bbac96455b6.png');
  background-size: cover;
  background-position: center;
  opacity: 1;
  background-color: #000;
  /*transition: transform 0.3s ease-out;*/
}

.imgdiv1 {
  position: absolute;
  top: 8%;
  width: 100%;
  text-align: center;
  line-height: 0;
}

.centerLogo {
  z-index: 3;
  width: 50%;
  height: 50%;
  display: inline-block;
  max-width: 100%;
}

.scrollDown {
  position: absolute;
  top: 24%;
  width: 100%;
  text-align: center;
  line-height: 0;
  animation: bounce 2s infinite;
}

.scrollInd {
  width: 15%;
  height: 15%;
  display: inline-block;
  max-width: 100%;
}

.contactSection {
  position: fixed;
  bottom: 2rem;
  right: 0%;
  width: 40%;
  height: 30%;
  z-index: 100;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  text-decoration: none;
  /*border: 1px solid red;*/
}

.contactSection:hover {
  opacity: 1;
}

.c-img {
  width: 20%;
  position: absolute;
  right: 0%;
  bottom: 2%;
  opacity: 1;
}

.c-img:hover {
  opacity: 1;
  cursor: pointer;
}

.contactCard {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 100;
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  font-size: 1rem;
  line-height: 1.5rem;
  padding: 1rem;
  max-width: 30rem;
}

.email {
  font-size: 15px;
}

.cardHeader {
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.contact-row {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.contact-label {
  font-weight: bold;
  margin-right: 0.5rem;
  flex-basis: 2rem;
}

.contact-text {
  flex-grow: 1;
}

.copy-button {
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  margin-left: 1rem;
  cursor: pointer;
}

.copy-button:hover {
  background-color: #555;
  }
  
.copy-status {
  margin-top: 0.5rem;
  font-style: italic;
  color: #888;
}

.close-button {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  border: none;
  background-color: transparent;
  font-size: 1.5rem;
  line-height: 1;
  cursor: pointer;
}

.close-button:hover {
  color: #ff0000;
}

.c-img.visble {
  opacity: 1;
}

.c-img.hidden {
  opacity: 0;
  pointer-events: none;
}

.contactCard.visible {
  opacity: 1;
}

.contactCard.hidden {
  opacity: 0;
  pointer-events: none;
}


.info-card {
  width: 100%;
  height: auto;
  min-height: 50%; /* Added min-height to accommodate the content */
  max-width: 100%;
  max-height: 100%;
  z-index: 1;
  text-align: center;
  padding-top: 30px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #fff;
  position: absolute;
  top: 10%;
}

.sec-title, .sec-paragraph {
  margin: 3 0px;
}

.sec-title {
  font-size: 80px;
  padding-bottom: 0px;
}

.sec-paragraph {
  font-size: 25px;
  padding-bottom: 9px;
  text-align: center;
  word-wrap: break-word;
  white-space: pre-line;
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  overflow-wrap: break-word;
}

.sec-background {
  margin-top: 0px;
  margin-bottom: 12px;
  z-index: 0;
  height: 100%;
  max-width: 100%;
  max-height: 50%;
  text-align: center;
}

.info-card, .sec-background {
  display: block;
}

.about-background {
  display: block;
  max-width: 100%;
}

.services-background {
  display: block;
  max-width: 100%;
}

.About {
  display: flex;
  flex-direction: column;
  min-height: 600px;
  height: 100%;
  background-color: #fff;
}

.About-img {
  display: flex;
  min-height: 70vh;
  background-image: url('https://cklandscaping.s3.amazonaws.com/static/media/SERV_MODIFIED.219366e75d562e5f1937.png');
  background-size: cover;
  background-position: center;
}

.Service-img {
  display: flex;
  min-height: 70vh;
  background-image: url('https://cklandscaping.s3.amazonaws.com/static/media/zg_lawnmower.8c67eae4aebf3125e6db.jpg');
  background-size: cover;
  background-position: center;
}

.Services {
  display: flex;
  flex-direction: column;
  min-height: 600px;
  height: 100%;
  background-color: #fff;
}

.service-block1 {
  width: 300px;
  height: 200px;
  background-color: #f0f0f0;
  padding: 20px;
  margin-bottom: 20px;
  box-sizing: border-box;
  /* Set the position of the block to absolute */
  position: absolute;
  /* Set top and left properties to position the blocks diagonally */
  top: -100px;
  left: calc(50% - 150px);
}

.bk-img-one {
  display: inline-block;
  width: 100px;
  height: 50%;
}



.service-block2 {
  width: 300px;
  height: 200px;
  background-color: rgb(179, 48, 48);
  padding: 20px;
  margin-bottom: 0px;
  box-sizing: border-box;
  display: inline-flex;
}



/* FOOTER */

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  background-color: #333;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer p {
  margin: 0;
  text-align: center;
}

.kwatt-plug {
  color: #7EB34F;
}

a {
  text-decoration: none;
  color: inherit;
}

